@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;700&display=swap);
html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font-size:100%;font:inherit;vertical-align:baseline}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}body{line-height:1}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after,q:before,q:after{content:'';content:none}table{border-collapse:collapse;border-spacing:0}

.home{height:100vh;padding:0 2rem;display:flex;align-items:center;justify-content:center;background:linear-gradient(0.25turn, #3f87a6, #ebf8e1, #f69d3c)}.home__container{width:40rem;height:40rem;padding:0 2rem;margin:0 auto;display:flex;flex-direction:column;justify-content:center;box-shadow:0px 0px 1rem 1px #888888;border-radius:1rem;background-color:white}.home__title{font-size:3.6rem;text-align:center;-webkit-user-select:none;-ms-user-select:none;user-select:none;font-weight:bold;color:#1871f2;margin-bottom:2rem}.home__btn{outline:none;border:none;font-size:2rem;padding:1.6rem 0;margin:1rem 0;color:white;font-weight:bold;border-radius:0.8rem;cursor:pointer}.home__btn:first-of-type{background-color:#1871f2}.home__btn:first-of-type:hover{background-color:#166fe5}.home__btn:last-of-type{background-color:#42b72a}.home__btn:last-of-type:hover{background-color:#36a420}

.messenger{height:100vh;padding:0 2rem;display:flex;align-items:center;justify-content:center;background:linear-gradient(0.25turn, #3f87a6, #ebf8e1, #f69d3c)}.messenger__container{width:40rem;height:40rem;margin:0 auto;padding:1rem 0;display:flex;flex-direction:column;box-shadow:0px 0px 1rem 1px #888888;border-radius:1rem;background-color:white}.messenger__title{padding:0.8rem 2rem;border-bottom:1px solid #D0D3D4;font-size:2rem;-webkit-user-select:none;-ms-user-select:none;user-select:none;font-weight:bold;color:#1871f2}.messenger__content{flex-grow:1;display:flex;flex-direction:column;margin-bottom:1rem;padding:1rem;overflow-y:auto;-webkit-overflow-scrolling:touch}.messenger__user,.messenger__friend{margin:1rem 0;display:flex;align-items:center}.messenger__user{flex-direction:row-reverse}.messenger__user .messenger__username{display:none}.messenger__message{font-size:1.6rem;padding:0.5rem 1.5rem;background-color:#E4E6EB;border-radius:2rem}.messenger__user .messenger__message{background-color:#0084ff;color:white}.messenger__username{margin-left:1rem;color:#7B7D7D;font-size:1.2em;-webkit-user-select:none;-ms-user-select:none;user-select:none}.messenger__form{display:flex}.messenger__input{flex-grow:1;outline:none;border:none;padding:1rem;margin:0 0.5rem;border-radius:2rem;background-color:#f0f2f5}.messenger__btn{padding:1rem;margin-right:0.5rem;border-radius:1rem;outline:none;border:none;background-color:#0084ff;color:white;cursor:pointer;font-weight:bold}.messenger__btn:hover{background-color:#166fe5}

.sign-in{height:100vh;padding:0 2rem;display:flex;align-items:center;justify-content:center;background:linear-gradient(0.25turn, #3f87a6, #ebf8e1, #f69d3c)}.sign-in__container{width:40rem;height:40rem;padding:0 2rem;margin:0 auto;display:flex;flex-direction:column;justify-content:center;box-shadow:0px 0px 1rem 1px #888888;border-radius:1rem;background-color:white}.sign-in__title{font-size:3.6rem;text-align:center;-webkit-user-select:none;-ms-user-select:none;user-select:none;font-weight:bold;color:#1871f2;margin-bottom:2rem}.sign-in__form{display:flex;flex-direction:column}.sign-in__input{outline:none;border:none;padding:1.6rem 1.6rem;font-size:1.6rem;margin:0.5rem 0;border:1px solid #B3B6B7;border-radius:1rem}.sign-in__input:focus{border-color:#1877f2;box-shadow:0 0 0 2px #e7f3ff}.sign-in__btn{outline:none;border:none;cursor:pointer;padding:1.6rem 1.6rem;font-size:2.4rem;font-weight:bold;color:white;border-radius:1rem}.sign-in__btn:first-of-type{margin-top:2rem;background-color:#1871f2}.sign-in__btn:first-of-type:hover{background-color:#166fe5}.sign-in__btn:last-of-type{margin-top:1rem;background-color:#42b72a}.sign-in__btn:last-of-type:hover{background-color:#36a420}

.sign-up{height:100vh;padding:0 2rem;display:flex;align-items:center;justify-content:center;background:linear-gradient(0.25turn, #3f87a6, #ebf8e1, #f69d3c)}.sign-up__container{width:40rem;height:40rem;padding:0 2rem;margin:0 auto;display:flex;flex-direction:column;justify-content:center;box-shadow:0px 0px 1rem 1px #888888;border-radius:1rem;background-color:white}.sign-up__title{font-size:3.6rem;text-align:center;-webkit-user-select:none;-ms-user-select:none;user-select:none;font-weight:bold;color:#1871f2;margin-bottom:2rem}.sign-up__form{display:flex;flex-direction:column}.sign-up__input{outline:none;border:none;padding:1.6rem 1.6rem;font-size:1.6rem;margin:0.5rem 0;border:1px solid #B3B6B7;border-radius:1rem}.sign-up__input:focus{border-color:#1877f2;box-shadow:0 0 0 2px #e7f3ff}.sign-up__btn{outline:none;border:none;cursor:pointer;margin-top:2rem;padding:1.6rem 1.6rem;font-size:2.4rem;font-weight:bold;background-color:#42b72a;color:white;border-radius:1rem}.sign-up__btn:hover{background-color:#36a420}

*,*:before,*:after{margin:0;padding:0;box-sizing:border-box;-webkit-box-sizing:border-box}html{font-size:62.5%;touch-action:none}body{font-family:"Poppins", sans-serif;line-height:1.4;font-weight:400;overflow:hidden !important}

